import React, { useState } from 'react'
import Swiper from 'react-id-swiper'
import NavBar from '../components/NavBar/NavBar'
import Intro from '../Slides/Intro/Intro'
import SlideOne from '../Slides/SlideOne/SlideOne'
import SlideTwo from '../Slides/SlideTwo/SlideTwo'
import SlideThree from '../Slides/SlideThree/SlideThree'
import SlideFour from '../Slides/SlideFour/SlideFour'
import SlideFive from '../Slides/SlideFive/SlideFive'
import SlideSix from '../Slides/SlideSix/SlideSix'
import 'swiper/css/swiper.css'
import './scroll.css'

export const Scroll = () => {
  const [swiper, updateSwiper] = useState(null)

  const params = {
    direction: 'horizontal',
    slidesPerView: 1,
    mousewheel: true,
    speed: 800,
    getSwiper: updateSwiper,
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
    pagination: {
      el: '.swiper-pagination',
      type: 'bullets',
      clickable: true,
    },
  }

  const intro = () => {
    if (swiper !== null) {
      swiper.slideTo(0)
    }
  }

  const slide1 = () => {
    if (swiper !== null) {
      swiper.slideTo(1)
    }
  }

  const slide2 = () => {
    if (swiper !== null) {
      swiper.slideTo(2)
    }
  }

  const slide3 = () => {
    if (swiper !== null) {
      swiper.slideTo(3)
    }
  }

  const slide4 = () => {
    if (swiper !== null) {
      swiper.slideTo(4)
    }
  }

  const slide5 = () => {
    if (swiper !== null) {
      swiper.slideTo(5)
    }
  }

  const slide6 = () => {
    if (swiper !== null) {
      swiper.slideTo(6)
    }
  }

  return (
    <>
      <NavBar
        intro={intro}
        slide1={slide1}
        slide2={slide2}
        slide3={slide3}
        slide4={slide4}
        slide5={slide5}
        slide6={slide6}
      />

      <Swiper {...params}>
        <div>
          <Intro />
        </div>
        <div>
          <SlideOne />
        </div>
        <div>
          <SlideTwo />
        </div>
        <div>
          <SlideThree />
        </div>
        <div>
          <SlideFour />
        </div>
        <div>
          <SlideFive />
        </div>
        <div>
          <SlideSix />
        </div>
      </Swiper>
    </>
  )
}
